import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import closeBtn from '../assets/img/close_button.png';
import lobbyIcon from '../assets/img/icon_back_lobby.png';
import soundIcon from '../assets/img/icon_sound.png';
import supportIcon from '../assets/img/icon_support.png';
import howPlayIcon from '../assets/img/icon_how_to_play.png';


const GlobalMenu = ({showGlobalMenu, toggle}) => {
    return(
        <Container className={showGlobalMenu ? "globalmenu showGlobalMenu": "hideGlobalMenu globalmenu"}>
        <Row>
            <Col className="menutitle">MENU</Col>
            <Col xs="3">
                <img className="closebtn" src={closeBtn} alt="close" onClick={toggle}/>
            </Col>
        </Row>
        <Row className="mt-3 menuitem">
            <Col>
                <img src={lobbyIcon} alt="lobby" /> <span>BACK TO LOBBY</span>
            </Col>
        </Row>
        <Row className="mt-2 menuitem">
            <Col>
                <img src={soundIcon} alt="lobby" /> <span>SOUND OFF</span>
            </Col>
        </Row>
        <Row className="mt-2 menuitem">
            <Col>
                <img src={supportIcon} alt="lobby" /> <span>SUPPORT</span>
            </Col>
        </Row>
        <Row className="mt-2 menuitem">
            <Col>
                <img src={howPlayIcon} alt="lobby" /> <span>HOW TO PLAY</span>
            </Col>
        </Row>
    </Container>
    
        );
}

export default GlobalMenu;