import React, { useState } from 'react';
import { Button, Modal, ModalBody, Container, Row, Col } from 'reactstrap';

const BackBetModal = (props) => {
  const {
    isOpen,
    toggle
  } = props;

  return (
    <div>
      <Modal isOpen={isOpen} toggle={toggle} className="betModal">
        <ModalBody>
          <Container>
              <Row className="modaltitle">
                  <Col className="title"><span>Place Bet</span></Col>
              </Row>
          </Container>
          <Container className="betModalBody">
            <Row className="backlayTitle">
                <Col className="backTitle">
                    BACK
                </Col>
                <Col className="layTitle">
                    LAY
                </Col>
            </Row>
            <Row className="mt-3 ticketRow">
                <Col className="ticket1">
                    <div>Ticket 1</div>
                    <div>80</div>
                </Col>
                <Col className="ticket2">
                    <div>Ticket 2</div>
                    <div>100</div>
                </Col>
            </Row>
            <Row className="mt-2 rowDivider"></Row>
            <Row className="bettitle">
                  <Col className="title"><span>Select Bet</span></Col>
            </Row>
            <Row className="mt-1 betticketRow">
                <Col className="betticket1">
                    <div>80</div>
                </Col>
                <Col className="betticket2">
                    <div>100</div>
                </Col>
            </Row>
            <Row className="mt-1 betticketRow">
                <Col className="betticket1">
                    <div>80</div>
                </Col>
                <Col className="betticket2">
                    <div>100</div>
                </Col>
            </Row>
            <Row className="mt-1 betticketRow">
                <Col className="betticket1">
                    <div>80</div>
                </Col>
                <Col className="betticket2">
                    <div>100</div>
                </Col>
            </Row>
            <Row className="mt-1 betticketRow mb-2">
                <Col className="betticket1">
                    <div>80</div>
                </Col>
                <Col className="betticket2">
                    <div>100</div>
                </Col>
            </Row>
          </Container>
          <Container>
          <Row className="fotterRow mt-3 mb-3">
                <Col className="cancelbtn" onClick={toggle}>
                    <div>Cancel</div>
                </Col>
                <Col className="submitbtn" onClick={toggle}>
                    <div>Submit</div>
                </Col>
            </Row>
          </Container>
        </ModalBody>
        </Modal>
    </div>
  );
}

export default BackBetModal;