import React from 'react';
import { Box } from 'reflexbox';

const HousieNumber = ({number}) => {
    return(
            <Box p={1} className={number === '08' || number === '55' || number === '76' ? 'housie-number-called' : 'housie-number'}>
                <span>{number}</span>
            </Box>
        );
}

export default HousieNumber;