import React, {useState} from 'react';
import { Flex } from 'reflexbox';
import { Container, Row, Col } from 'reactstrap';
import TicketRow from '../component/ticketRow'; 
import menuIcon from '../assets/img/menu_icon.png';
import shuffleButton from '../assets/img/shuffle_button.png';
import playButton from '../assets/img/play_button.png';
import GlobalMenu from '../component/globalMenu';
import './main.css';

const GameContainer = () => {
    const [numberArray, setNumberArray] = useState(['01','','03','04','05','','07','','09'])
    const [numberArray1, setNumberArray1] = useState(['01','','03','04','05','','','08','09'])
    const [numberArray2, setNumberArray2] = useState(['01','02','03','','05','','07','','09'])
    const [showGlobalMenu, setShowGlobalMenu] = useState(false);

    const showMenu = () => {
        setShowGlobalMenu(!showGlobalMenu)
    }
    const shuffleTickets = () => {
        const temp = numberArray;
        setNumberArray(numberArray1);
        setNumberArray1(numberArray2);
        setNumberArray2(temp);
    }
    const playGame = () =>{
        window.location.href = "#/play";
    }
    
    return (
        <div className="gamebg">
            <GlobalMenu showGlobalMenu={showGlobalMenu} toggle={showMenu}/>
            <Container>
                <Row className="mb-5">
                    <Col className="gameLogo">Game Logo</Col>
                    <Col className="userDetails">User Name</Col>
                    <Col className="menuIcon" onClick={showMenu}>
                        <img src={menuIcon} alt="menuIcon"/>
                    </Col>
                </Row>
                <Row className="mb-3">
                    <Col className="selectTitle">Select Your Tickets!</Col>
                </Row>
            </Container>
            
            <Flex className="ticket-wrapper">
                <div className="tickerTitle">Ticket 02</div>
                <div className="ticketLabel">A 20</div>
                    <div className="number-wrapper">
                        <TicketRow key="1" row={numberArray} className="first-ticket-row"/>
                        <TicketRow key="2" row={numberArray1}/>
                        <TicketRow key="3" row={numberArray2} className="last-ticket-row"/>
                    </div>
            </Flex>
            <Flex className="ticket-wrapper mt-4">
            <div className="tickerTitle">Ticket 02</div>
            <div className="ticketLabel">A 20</div>
                <div className="number-wrapper">
                    <TicketRow key="4" row={numberArray} className="first-ticket-row"/>
                    <TicketRow key="5" row={numberArray1}/>
                    <TicketRow key="6" row={numberArray2} className="last-ticket-row"/>
                </div>
            </Flex>
            <Container>
                <Row className="mt-4">
                    <Col className="shuffleIcon" onClick={shuffleTickets}>
                        <img src={shuffleButton} alt="shuffle"/>
                    </Col>
                </Row>
                <Row className="mt-2">
                    <Col className="playIcon" onClick={playGame}>
                        <img src={playButton} alt="play"/>
                    </Col>
                </Row>
                <Row className="onlineText">
                    <Col>
                        Online players : 12200
                    </Col>
                </Row>
            </Container>
        </div>
    )
}

export default GameContainer;