import React from 'react';
import { Button } from 'reactstrap';
import './main.css';

const HousieMainContainer = () => {
    const housieGame = () =>{
        window.location.href = "#/game";
    }
    return (
        <div className="main-container">
            <h1 className="mt-1 mb-3"> Game Lobby</h1>
            <Button color="primary" onClick={housieGame}>Housie Game Icon</Button>
        </div>
    )
}

export default HousieMainContainer;