import React, {useState} from 'react';
import { Flex } from 'reflexbox';
import { Container, Row, Col } from 'reactstrap';
import TicketRow from '../component/ticketRow'; 
import menuIcon from '../assets/img/menu_icon.png';
import backIcon from '../assets/img/arrow_back.png';
import layIcon from '../assets/img/arrow_lay.png';
import eyeDisabled from '../assets/img/eye_disabled.png';
import eyeEnabled from '../assets/img/eye_click.png';
import backBetIcon from '../assets/img/back_bet_button.png';
import layBetIcon from '../assets/img/lay_bet_button.png';
import GlobalMenu from '../component/globalMenu';
import BackBetModal from '../component/backBetModal';
import LayBetModal from '../component/layBetModal';

import './main.css';

const PlayContainer = () => {
    const [showGlobalMenu, setShowGlobalMenu] = useState(false);
    const [openLay, setOpenLay] = useState(false);
    const [openBack, setBackLay] = useState(false);

    const openBetModal = () => {
        setBackLay(true);
    }
    const toggleBetModal = () => {
        setBackLay(false);
    }
    const openLayBetModal = () => {
        setOpenLay(true);
    }
    const toggleLayBetModal = () => {
        setOpenLay(false);
    }
    

    const showMenu = () => {
        setShowGlobalMenu(!showGlobalMenu)
    }
    const [numberArray, setNumberArray] = useState(['01','','03','04','05','','07','','09'])
    const [numberArray1, setNumberArray1] = useState(['01','','03','04','05','','','08','09'])
    const [numberArray2, setNumberArray2] = useState(['01','02','03','','05','','07','','09'])
    const housie = () => {
        window.location.href = "#/housie";
    }
    const tickets = () => {

    }
    const stats = () => {
        
    }
    return (
        <>
        <div className="gamebg">
            <GlobalMenu showGlobalMenu={showGlobalMenu} toggle={showMenu}/>
            <Container>
                <Row className="mb-5">
                    <Col className="gameLogo">Game Logo</Col>
                    <Col className="userDetails">User Name</Col>
                    <Col className="menuIcon">
                        <img src={menuIcon} alt="menuIcon" onClick={showMenu}/>
                    </Col>
                </Row>
            </Container>
            
            <Flex className="ticket-wrapper">
                <div className="tickerTitle">Ticket 02</div>
                <div className="ticketLabel">A 20</div>
                    <div className="number-wrapper">
                        <TicketRow key="1" row={numberArray} className="first-ticket-row"/>
                        <TicketRow key="2" row={numberArray1}/>
                        <TicketRow key="3" row={numberArray2} className="last-ticket-row"/>
                    </div>
            </Flex>
            <Flex className="ticket-wrapper mt-4">
            <div className="tickerTitle">Ticket 02</div>
            <div className="ticketLabel">A 20</div>
                <div className="number-wrapper">
                    <TicketRow key="4" row={numberArray} className="first-ticket-row"/>
                    <TicketRow key="5" row={numberArray1}/>
                    <TicketRow key="6" row={numberArray2} className="last-ticket-row"/>
                </div>
            </Flex>
        </div>
        <div className="betCard3">
            <Container>
                    <Row className="mt-2 ml-2">
                        <span className="cardTitle">Full House Market</span> <img className="cardTopImg3" src={eyeEnabled} alt="eye"/>
                    </Row>
            </Container>
        </div>
        <div className="betCard2">
            <Container>
                    <Row className="mt-2 ml-2">
                        <span className="cardTitle">First Line Market</span> <img className="cardTopImg2" src={eyeEnabled} alt="eye"/>
                    </Row>
            </Container>
        </div>
        
        <div className="betCard">
            <Container>
                <Row className="mt-2 ml-2">
                    <span className="cardTitle">Corner Market</span> <img className="cardTopImg" src={eyeDisabled} alt="eye"/>
                </Row>
                <Row className="betTitle">
                    <Col></Col>
                    <Col><img className="mr-2" src={backIcon} alt="back"/>BACK</Col>
                    <Col><img className="mr-2" src={layIcon} alt="back"/>LAY</Col>
                    <Col>PAYS</Col>
                    {/* <span className="back"><img className="mr-2" src={backIcon} alt="back"/>BACK</span>
                    <span className="back"><img className="mr-2" src={layIcon} alt="back"/>LAY</span>
                    <span className="paysTitle">PAYS</span> */}
                </Row>
                <Row className="betValues mb-2 mt-2">
                    <Col className="betTicketValue">Ticket 01</Col>
                    <Col onClick={openBetModal}>
                        <img src={backBetIcon} alt="backbet"/>
                        <span className="betvalue">$200</span>
                    </Col>
                    <Col onClick={openLayBetModal}>
                        <img src={layBetIcon} alt="backbet"/>
                        <span className="betvalue">$200</span>
                    </Col>
                    <Col className="betTicketValueRs">$1000</Col>
                </Row>
                <Row className="betValues">
                    <Col className="betTicketValue">Ticket 02</Col>
                    <Col onClick={openBetModal}>
                        <img src={backBetIcon} alt="backbet"/>
                        <span className="betvalue">$200</span>
                    </Col>
                    <Col onClick={openLayBetModal}>
                        <img src={layBetIcon} alt="backbet"/>
                        <span className="betvalue">$200</span>
                    </Col>
                    <Col className="betTicketValueRs">$1000</Col>
                </Row>
            </Container>
        </div>
        <div>
            <div className="bottomBar">
                <Col onClick={housie}>Housie</Col>
                <Col className="selected" onClick={tickets}>Tickets</Col>
                <Col onClick={stats}>Stats</Col>
            </div>
        </div>
        <BackBetModal isOpen={openBack} toggle={toggleBetModal}/>
        <LayBetModal isOpen={openLay} toggle={toggleLayBetModal}/>
        
        </>
    )
}

export default PlayContainer;