import React from 'react';
import { HashRouter, Switch, Redirect, Route } from 'react-router-dom';
import GameContainer from './gameContainer';
import HousieMainContainer from './housieMainContainer';
import PlayContainer from './playContainer';
import HousiePlayContainer from './housiePlayContainer';
import './main.css';

const MainContainer = (props) => {
    return (
        <HashRouter>
            <div className="main-container">
                <Switch>
                    <Route path='/main' component={HousieMainContainer} />
                    <Route path='/game' component={GameContainer} />
                    <Route path='/play' component={PlayContainer} />
                    <Route path='/housie' component={HousiePlayContainer} />
                    <Redirect to='/main' />
                </Switch>
            </div>
        </HashRouter>
    )
}

export default MainContainer;