import React from 'react';
import { Flex } from 'reflexbox';
import HousieNumber from './housieNumber';

const HousieRow = ({row, className}) => {
    const classes = `housieRow ${className}`;
    return(
            <Flex className={classes}>
                {
                    row.map(function(number, index){
                        return (
                            <HousieNumber key={`a${number}${index}`} number={number}/>
                        );
                    })
                }
            </Flex>
        );
}

export default HousieRow;